@import url('https://fonts.googleapis.com/css?family=Labrada|Wallpoet|VT323|Ubuntu|Julee|Lora|Kalam|Merienda');
body{
  background-image: url("back.jpg");
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  background-attachment: fixed;
}
.App{
  padding-left: 10vw;
  padding-right: 10vw;
  display: grid;
  grid-template-rows: 3cm auto;
  row-gap: 1cm;
}
.addTodoForm{
  display: flex;
  column-gap: .2cm;
}
.addTodoInput{
  width: 8cm;
  height: 1cm;
  font-size: 25px;
  border: none;
  background-color: #D6F1FF;
  border-radius: 10px;
}
.addTodoInput:hover{
  border: 1px solid black;
}
.signIn{
  display: flex;
  column-gap: 1vw ;

  flex-wrap: wrap;
}
.leftSignIn{
  width: 39vw;
  /* border: 1px solid black; */
 
}
.rightSignIn{
  width: 37vw;
  /* border: 1px solid black; */
}
.tasksSvg{
  width: 37vw;
}
.tagLine{
  font-size: 40px;
  margin: 5px 0px;
  font-weight: 600;
  font-family: Lora;
}
.headLine{
  font-size: 50px;
  margin: 5px 0px;
  font-weight:600;
  font-family: Lora;
  color: blueviolet;
}
.rightUpper{
  display: flex;
  align-items: center;
  column-gap: .5cm;
  
}
.menuItem{
  font-size: 20px;
  font-family: Ubuntu;
  cursor: pointer;
}
.addTodoButton{
  width: 2cm;
  height: 1cm;
  background-color: blueviolet;
  color: whitesmoke;
  font-size: 25px;
  font-weight: bold;
  border-radius: 5px;
  border: none;
  cursor: pointer;
}
.todoList{
  display: flex;
  column-gap: .3cm;
  align-items: center;
  
  
}
.next{
  width: .3cm;
  height: .3cm;
}
.todossss{
  display: flex;
  align-items: center;
  column-gap: .1cm;
}
.delTodo{
  width: .7cm;
  height: .7cm;
  cursor: pointer;
}
.todoName{
  margin: 5px 0px;
  text-align: left;
}
.sign-out-one{
  width: 2.5cm;
  height: .8cm;
  font-size: 16px;
  background-color: #28282b;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.sign-out-two{
  width: 2.5cm;
  height: .8cm;
  font-size: 16px;
  background-color: #28282b;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}
.sign-in{
  width: 8cm;
  height: 1.2cm;
  font-size: 25px;
  background-color: blueviolet;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  /* margin-left: 20%; */
}
.userPhoto{
  width: 1.2cm;
  height: 1.2cm;
  border-radius: 50px;
}
.userPhotoSection{
  display: flex;
 
  align-items: center;
  column-gap: .2cm;
  position: sticky;
}
.subTagLine{
  font-size: 30px;
  margin: 5px 0px;
  font-weight: 600;
  font-family: Lora;
}
.logoText{
  font-family: Julee;
  font-size: 50px;
}
.upper{
  display: flex;
  align-items:center ;
  justify-content: space-between;
}
.menuImg{
  width: .8cm;
  display: none;
}

.overlay {
  height: 100%;
  width: 0%;
  position: fixed;
  z-index: 1;
  top: 0;
  right: 0;
  background-color:#adadad;
/*  background-color: rgba(0,0,0, 0.9);*/
  overflow-y: hidden;
  transition: 0.5s;
}
.overlay-content {
  position: relative;
  top: 15%;
  width: 100%;
  text-align: center;
  margin-top: 30px;
}

.overlay a {
  padding: 8px;
  text-decoration: none;
  
  display: block;
  transition: 0.3s;
}

.overlaya{
  font-size: 28px;
  color: black;
}

.overlay a:hover, .overlay a:focus {
  color: #f1f1f1;
}

.overlay .closebtn {
  position: absolute;
  top: 10px;
  right: 45px;
  font-size: 60px;
}

.coffee{
  width: 1.5cm;
  height: 1.5cm;
 
  z-index: 1000;
  top: 85%;
  left: 86%;
  position: fixed;
  border-radius: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: blueviolet;
  box-shadow:rgba(0,0,0,0.5) 0px 5px 15px, inset rgba(0,0,0,0.1) 0px -10px 20px;
  cursor: pointer;

}

.coffeeImg{
  width: 1cm;
  height: 1cm;
  cursor: pointer;

  
}

#kofiframe{
  z-index: 400;
  height: 0vh;
	width: 380px;
  left: 32%;
  top: 8%;
  position: fixed;
 
  border: none;
	border-radius: 10px;
	
  /* transition: 0.1s; */
}

.closebtn2{
  z-index: 800;
  position: fixed;
  left: 52%;
  top: 1%;
  font-size: 50px;
  display: none;
  color: red;
  cursor: pointer;
}

@media screen and (max-width:1350px){
 .tagLine{
  font-size: 35px;
 }
 .headLine{
  font-size: 45px;
 }
 .subTagLine{
  font-size: 25px;
 }
}

